import React, { useContext, useEffect, useRef, useState } from 'react';
import { CSSTransition } from 'react-transition-group';

import * as firebaseui from 'firebaseui';
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';

import { useAuthState } from 'react-firebase-hooks/auth';
import { useObjectVal } from 'react-firebase-hooks/database';

import { firebase, GlobalContext, Components } from './root';
import Chat from './Chat';

import uistrings from './uistring.json';
import './App.css';


function Icon({ name }) {
    return name?.includes?.('https') ? <img alt="" draggable="false" className="user-image" src={name} /> : <i className="material-icons-round">{name}</i>;
}

function App({ keys, accessibility }) {

    const [user, loadingUser] = useAuthState(firebase.auth());
    const [strikes, loadingStrikes] = useObjectVal(firebase.database().ref(`users/${user?.uid}/strikes`));
    const banned = loadingStrikes ? null : strikes > 2;
    // console.log(banned, strikes, `users/${user?.uid}/strikes`);

    useEffect(() => {
        if (!user || user.isAnonymous) return;
        
        (async () => {
            const data = user?.providerData?.[0] ?? user;
            const ref = firebase.storage().ref('users/' + user.uid + '.jpg');
            if ((await ref.parent.listAll()).items.map(e => e.location.path).includes('users/' + user.uid + '.jpg')) return;
            const res = await fetch(data.photoURL);
            if (!res.ok) return;
            await ref.put(await res.blob());

            firebase.database().ref('users/' + user.uid).set({
                displayName: data.displayName,
                photoURL: await ref.getDownloadURL()
            });
        })();
    }, [user]);

    const [state, setState] = useState({
        sidebar: false,
        location: 0,
        accessibility: {
            ...Object.fromEntries(Object.values(keys).map(e => [e, null])),
        },
        
        ...JSON.parse(window.localStorage.getItem('state'))
    });
    
    useEffect(() => {
        const savedKeys = ['location', 'accessibility'];
        window.localStorage.setItem('state', JSON.stringify(Object.fromEntries(Object.entries(state).filter(([k]) => savedKeys.includes(k)))));
    }, [state]);
    
    const getMedia = key => state.accessibility[key] ?? window.matchMedia(accessibility[key][1]).matches;

    const loadMedia = (key, value) => {
        const [media, original] = accessibility[key];
        switch (value) {
            case window.matchMedia(original).matches: media.mediaText = original; return null;
            case true: media.mediaText = 'all'; return true;
            default: media.mediaText = 'not all'; return false;
        }
    };
    
    const toggleMedia = key => {
        const newState = state;
        if (loadMedia(key, newState.accessibility[key] = !getMedia(key)) === null) newState.accessibility[key] = null;
        setState({ ...newState });
    };
    
    Object.keys(accessibility).forEach(key => loadMedia(key, getMedia(key)));

    const autoLogin = firebase.auth().isSignInWithEmailLink(window.location.href);

    return (
        <GlobalContext.Provider value={{ loading: loadingUser || loadingStrikes, user, banned }}>
            <Sidebar visible={state.sidebar}>
                <SidebarItem leftIcon="airplanemode_on" onClick={() => setState({ ...state, location: 0 })}>{uistrings.sidebar_default}</SidebarItem>
                <SidebarItem leftIcon="airplanemode_on" onClick={() => setState({ ...state, location: 1 })}>{uistrings.sidebar_default}</SidebarItem>
                <SidebarItem leftIcon="chat" onClick={() => setState({ ...state, location: 2 })}>{uistrings.sidebar_chat}</SidebarItem>
            </Sidebar>
            <Navbar>
                <NavItem icon="menu" persistent={true} onClick={e => setState({ ...state, sidebar: e })} />
                <Filler />
                <NavItem icon={'notifications' + (strikes ? '_active' : '')} badge={strikes}>
                    <DropdownMenu>
                        {((loadingUser || loadingStrikes) && <Components.Loading />) ||
                        (banned && <DropdownItem leftIcon="error">{uistrings.menubar_status_banned}</DropdownItem>) ||
                        (strikes && <DropdownItem leftIcon="warning">{uistrings.menubar_status_strikes.replace('{1}', strikes)}</DropdownItem>) ||
                        (<DropdownItem leftIcon="thumb_up">{uistrings.menubar_status_ok}</DropdownItem>)
                        }
                    </DropdownMenu>
                </NavItem>
                <NavItem icon="account_circle" persistent={false} autoOpen={autoLogin}>
                    <DropdownMenu>
                        <UserDropdown autoLogin={autoLogin} />
                    </DropdownMenu>
                </NavItem>
                <NavItem icon="keyboard_arrow_down">
                    <DropdownMenu>
                        <DropdownItem leftIcon="bedtime" onClick={() => toggleMedia('darkTheme')} rightIcon={<Components.Switch on={getMedia('darkTheme')} />}>{uistrings.menubar_accessibility_theme}</DropdownItem>
                        <DropdownItem leftIcon="double_arrow" onClick={() => toggleMedia('reducedMotion')} rightIcon={<Components.Switch on={getMedia('reducedMotion')} />}>{uistrings.menubar_accessibility_motion}</DropdownItem>
                    </DropdownMenu>
                </NavItem>
            </Navbar>
            <Main page={state.location}>
                <span>{uistrings.page1}</span>
                <span>{uistrings.page2}</span>
                <Chat />
            </Main>
        </GlobalContext.Provider>
    );
}


function UserDropdown({ autoLogin }) {

    const { loading, user, banned } = useContext(GlobalContext);
    const [login, setLogin] = useState(autoLogin ?? false);

    if (loading) return <Components.Loading />;

    if (!user && !login) return <DropdownItem leftIcon="login" rightIcon="keyboard_arrow_right" onClick={() => setLogin(true)}>{uistrings.menubar_account_login}</DropdownItem>;
    
    if (!user || login) {
        const uiConfig = {
            autoUpgradeAnonymousUsers: true,
            callbacks: {
                signInSuccessWithAuthResult: (authResult, redirectUrl) => {
                    setLogin(false);
                },
                signInFailure: async error => {
                    if (error.code !== 'firebaseui/anonymous-upgrade-merge-conflict') return Promise.resolve();
                    setLogin(false);

                    firebase.auth().currentUser.delete();
                    await firebase.auth().signInWithCredential(error.credential);

                    // const newUser = firebase.auth().currentUser;

                    const result = await firebase.functions().httpsCallable('upgradeUser')({ newUid: user.uid });
                    console.log(result.data);
                    return;

//                     firebase.auth().currentUser.delete();
//                     console.log(firebase.auth().currentUser);
//                     console.log(error.credential);
//                     await firebase.auth().signInWithCredential(error.credential);

//                     const newUser = firebase.auth().currentUser;
//                     const messages = (await firebase.database().ref('messages').once('value')).val();
//                     const keys = Object.keys(messages).filter(mid => messages[mid].uid === user.uid);
//                     const updates = Object.fromEntries(keys.map(mid => [mid, { ...messages[mid], uid: newUser.uid }]));
// console.log(updates);
//                     const data = newUser?.providerData?.[0] ?? newUser;
//                     // const strikes = (await firebase.database().ref('users/' + user.uid + '/strikes').once('value')).val();

//                     firebase.database().ref('messages').update(updates);
//                     firebase.database().ref('users/' + newUser.uid).set({
//                         displayName: data.displayName,
//                         photoURL: data.photoURL,
//                         // strikes: strikes
//                     });
                },
                signInSuccessUrl: false,
            },
            credentialHelper: firebaseui.auth.CredentialHelper.GOOGLE_YOLO,
            signInFlow: firebase.auth().isSignInWithEmailLink(window.location.href) ? 'redirect' : 'popup',
            signInOptions: [
                {
                    provider: firebase.auth.EmailAuthProvider.PROVIDER_ID,
                    signInMethod: firebase.auth.EmailAuthProvider.EMAIL_LINK_SIGN_IN_METHOD,
                },
                {
                    provider: firebase.auth.GoogleAuthProvider.PROVIDER_ID,
                    clientId: '837019211462-a62qlmg12ln01fs4ubru65grnp86afup.apps.googleusercontent.com',
                },
                firebase.auth.FacebookAuthProvider.PROVIDER_ID,
                firebase.auth.TwitterAuthProvider.PROVIDER_ID,
                firebase.auth.GithubAuthProvider.PROVIDER_ID,
                firebase.auth.PhoneAuthProvider.PROVIDER_ID,
                !user && firebaseui.auth.AnonymousAuthProvider.PROVIDER_ID,
            ],
        };

        return <StyledFirebaseAuth uiConfig={uiConfig} firebaseAuth={firebase.auth()} />;
    }

    const data = user.providerData?.[0] ?? user;

    return <>
        <DropdownItem leftIcon={data.photoURL ?? 'person'}>{data.displayName ?? 'Anonymous'}</DropdownItem>
        {user.isAnonymous && !banned  && <DropdownItem leftIcon="upgrade" onClick={() => setLogin(true)}>{uistrings.menubar_account_upgrade}</DropdownItem>}
        {data.email && <DropdownItem leftIcon="alternate_email">{data.email}</DropdownItem>}
        {data.phoneNumber && <DropdownItem leftIcon="phone">{data.phoneNumber}</DropdownItem>}
        <DropdownItem leftIcon="close" onClick={async () => await firebase.auth().signOut()}>{uistrings.menubar_account_logout}</DropdownItem>
    </>;
}

function Sidebar(props) {
    const nodeRef = useRef();

    return (
        <CSSTransition nodeRef={nodeRef} in={props.visible} timeout={200} classNames="sidebar">
            <aside ref={nodeRef} className="sidebar">
                <ul className="sidebar-nav">{props.children}</ul>
            </aside>
        </CSSTransition>
    );
}

function SidebarItem(props) {
    return (
        <div className="clickable side-item" onClick={props.onClick}>
            {props.leftIcon && <span className="icon-button"><Icon name={props.leftIcon} /></span>}
            {props.children}
            {props.rightIcon && <span className="icon-right"><Icon name={props.rightIcon} /></span>}
        </div>
    );
}

const DropdownMenu = React.forwardRef((props, ref) => <div className="dropdown" ref={ref} {...props} />);

function DropdownItem(props) {
    return (
        <div className="clickable drop-item" onClick={props.onClick}>
            {props.leftIcon && <span className="icon-button"><Icon name={props.leftIcon} /></span>}
            {props.children}
            {props.rightIcon && <span className="icon-right"><Icon name={props.rightIcon} /></span>}
        </div>
    );
}

function Navbar(props) {
    return (
        <header className="navbar">
            <ul className="navbar-nav">{props.children}</ul>
        </header>
    );
}

const Filler = () => <li style={{ margin: 'auto' }} />;

function NavItem(props) {

    const [open, setOpen] = useState(props.autoOpen ?? false);
    const ref = useRef();
    const ref2 = useRef();

    const onClick = ({ target }) => !props.persistent && !ref.current?.contains?.(target) && !ref2.current?.contains?.(target) && !target.className.includes('firebaseui') && setOpen(false);

    useEffect(() => {
        document.addEventListener('mousedown', onClick);
        return () => document.removeEventListener('mousedown', onClick);
    });

    return (
        <li className={'nav-item' + (open ? ' open' : '')} ref={ref}>
            <div className="clickable icon-button" onClick={() => { setOpen(!open); props.onClick?.(!open); }}>
                <Icon name={props.icon} />
            </div>
            {props.badge && <span className="nav-badge">{props.badge}</span>}

            {open && props.children && React.cloneElement(props.children, { ref: ref2 })}
        </li>
    );
}

function Main(props) {
    return (
        <section className="main">
            <div className="main-container">{props.children[props.page ?? 0]}</div>
        </section>
    );
}

export default App;