import React, { useContext, useEffect, useRef } from 'react';

import { useListVals, useObjectVal } from 'react-firebase-hooks/database';

import { firebase, GlobalContext, Components } from './root';

import uistrings from './uistring.json';
import './Chat.css';


function Icon(props) {
    return /https:\/\//.test(props.src) ? <img alt="" draggable="false" {...{ ...props, className: 'msg-image' + (props.className ? ' ' + props.className : '') }} /> : <i {...{ ...props, className: 'material-icons-round msg-image ' + props.className }}>{props.src ?? 'person'}</i>;
}

function Chat() {

    const { user, loadingUser } = useContext(GlobalContext);
    
    return loadingUser || user ? <ChatRoom /> : <SignIn />;
}

function ChatRoom() {

    const { loading, user, banned } = useContext(GlobalContext);
    const scrolldown = useRef();

    const [messages, loadingMessages] = useListVals(firebase.database().ref('messages'));
    const [users, loadingUsers] = useObjectVal(firebase.database().ref('users'));

    useEffect(() => scrolldown.current && (scrolldown.current.parentNode.scrollTop = scrolldown.current.offsetTop));
    useEffect(() => console.log(loadingMessages, messages), [messages, loadingMessages]);

    return (
        <div className="chat">
            <span className="chat-header">{uistrings.chat_title}</span>
            <div className="chat-body">
                {loading || loadingMessages || loadingUsers ? <Components.Loading /> :
                (banned ? <ChatMessage message={{ text: uistrings.chat_message_banned_text, displayName: uistrings.chat_message_banned_user, photoURL: 'verified_user' }} /> :
                messages?.map((msg, i, a) => {
                    const connect = [];
                    if (a[i-1]?.uid === msg.uid) connect.push('top');
                    if (a[i+1]?.uid === msg.uid) connect.push('bottom');
                    return <ChatMessage key={i} meta={{ sent: msg.uid === user.uid, connect: connect }} message={{ ...msg, ...users[msg.uid] }} />
                }))}
                <div ref={scrolldown} />
            </div>
            <ChatBar />
        </div>
    );
}

function ChatMessage({ meta, message: msg }) {
    return (
        <div className={'message ' + (meta?.sent ? 'sent' : 'received') + meta?.connect?.map?.(e => ' connect-' + e)?.join?.('') }>
            {!meta?.connect?.includes('top') && <Icon src={msg?.photoURL} title={msg?.displayName ?? uistrings.chat_message_default_user} />}
            <span className="msg-text">{msg?.text}</span>
        </div>
    );
}

function ChatBar() {

    const { user } = useContext(GlobalContext);
    const input = useRef();

    const sendMessage = e => {
        if ((e.key && e.key !== 'Enter') || /^\s*$/.test(input.current.value)) return;

        firebase.database().ref('messages/').push({
            uid: user.uid,
            timestamp: firebase.database.ServerValue.TIMESTAMP,
            text: input.current.value
        });
        input.current.value = '';
    };

    const data = user?.providerData?.[0] ?? user ?? {};

    return (
        <div className="chat-footer">
            <Icon src={data.photoURL} title="You" />
            <input className="chat-input" type="text" placeholder={data.displayName ? uistrings.chat_input_placeholder_user.replace('{1}', data.displayName) : uistrings.chat_input_placeholder_default} onKeyDown={sendMessage} ref={input} />
            <button className="chat-send" onClick={sendMessage}>{uistrings.chat_button_send}</button>
        </div>
    );
}

function SignIn() {
    return <span>{uistrings.chat_error_account}</span>;
}

export default Chat;