import React from 'react';
import ReactDOM from 'react-dom';

import App from './App';


const keys = {
    '(prefers-color-scheme: dark)': 'darkTheme',
    '(prefers-reduced-motion: reduce)': 'reducedMotion'
};
const accessibility = Object.fromEntries([...document.styleSheets].flatMap(e => [...e.rules]).filter(e => e instanceof CSSMediaRule).map(({ media: m }) => [keys[m.mediaText], [m, m.mediaText.toString()]]));

ReactDOM.render(
    <React.StrictMode>
        <App keys={keys} accessibility={accessibility} />
    </React.StrictMode>,
    document.getElementById('root')
);